<!-- 用户登录界面 -->
<template>
  <div id="login">
    <div class="bg"></div>
    <el-row class="main-container">
      <el-col :lg="8" :xs="16" :md="10" :span="10">
        <div class="top">
          <i class="iconfont icon-kaoshi"></i><span class="title">在线考试系统</span>
        </div>
        <div class="bottom">
          <div class="container">
            <p class="title">账号登录</p>
            <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
              <el-form-item label="用户名">
                <el-input v-model.number="formLabelAlign.username" placeholder="请输入用户名"></el-input>
              </el-form-item>
              <el-form-item label="密码">
                <el-input v-model="formLabelAlign.password" placeholder="请输入密码" type='password'></el-input>
              </el-form-item>
              <!--邮箱验证    点击按钮跳出防机器人的操作-->
              <el-form-item label="验证码" >
                <el-input v-model="formLabelAlign.verify" placeholder="请输入验证码" style="width: 120px;" v-show="isShow"></el-input>
                <el-button @click="send" type="primary" style="width: 80px;margin-left: 30px;padding-right: 80px;" v-show="isShow">点我发送</el-button>
                <el-input v-model="formLabelAlign.verify" placeholder="请输入验证码" style="width: 120px;" v-show="noShow"></el-input>
                <!-- 倒计时 -->
                <el-button  type="primary" v-show="noShow" style="width: 80px;margin-left: 30px;padding-left: 40px;padding-right: 40px; text-align: center;" >{{ time }}</el-button>
                <el-dialog
                    title="请把图片旋转到正常角度"
                    :visible.sync="isTouch"
                    width="20%"
                    :before-close="handleClose">
                    <div>
                        <img :style="{ transform: 'rotate(' + rotate + 'deg)' }"
                        :src="imageUrl"
                        style="width: 30%;height: 30%;
                        border-radius: 50%;margin-left: 70px;"
                          />
                      <el-slider v-model="value3" :min=0 :max=360 :show-tooltip="false"
                      @input="all($event)"
                      @change="changeSlider($event)"></el-slider>
                    </div>
                </el-dialog>
                <!-- <div style=""></div> -->
                <el-input  v-model="formLabelAlign.verify" placeholder="请输入验证码" style="width: 120px;float: left;" v-show="imageShow"></el-input>
                <el-image  :src="imageSrc"   v-show="imageShow" @click="showImage" style="float: right; width: 100px;height: 38px; margin-left: 50px;" title="点我刷新" ></el-image>
              </el-form-item>

              <div class="submit">
                <el-button type="primary" class="row-login" @click="login()">登录</el-button>
              </div>
              <!-- <div class="options">
                <p class="find"><a href="javascript:;">找回密码</a></p>
                <div class="register">
                  <span>没有账号?</span>
                  <span><a href="javascript:;">去注册</a></span>
                </div>
              </div> -->
            </el-form>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row class="footer">
      <el-col>
        <p class="msg2">版权所有 ©2023 保留所有权利  <a href="http://beian.miit.gov.cn/" target="_blank">赣ICP备19001371号</a></p>
      </el-col>
    </el-row>
    <!-- <section class="remind">
      <span>管理员账号：9527</span>
      <span>教师账号：20081001</span>
      <span>密码都是：123456</span>
    </section> -->
  </div>
</template>

<script>
import { __DEV__ } from 'echarts/lib/config';
import {mapState} from 'vuex'
export default {
  name: "login",
  data() {
    return {
      rotate: Math.floor(Math.random()*100+220),
      value3:0,
      isShow:true,
      noShow:false,
      role: 2,
      valid:1111,//后续要改
      time: 60,
      isTouch:false,
      imageSrc:'',//图片验证码
      imageUrl:'',//旋转图片
      imageShow:false,
      countdown:false,
      labelPosition: 'left',
      formLabelAlign: {
        username: '20154084',
        password: '123456',
        verify:''
      }
    }
  },
  // created(){
  //   // this.rotate=Math.floor(Math.random()*130+120)//120--250
  //   // console.log(this.rotate)
  //   // this.$cookies.set("rotate",this.rotate)
  //   // this.downTime()
  //   this.showImage()
  // },
  methods: {
    showImage(){
      this.$axios({
          url: `/api/getVerifiCodeImage`,
          method: 'get',
          responseType: 'blob'
          }).then(res => {
            const url = URL.createObjectURL(new Blob([res.data]));
            console.log(res.data)
          this.imageSrc=url
        })
    },



    downTime(){

      var timer=setInterval(() => {
        this.time--
        this.countdown=true
        if(this.time==0){
          clearInterval(timer)
          this.time=60
          this.noShow=false
          this.isShow=true
          this.countdown=false
        }
      }, 1000);

    },
    handleClose(done) {
        done()
      },
    send(){
      this.isTouch=true
      this.rotate=Math.floor(Math.random()*130+120)
      this.$cookies.set("rotate",this.rotate)
      this.$axios({
          url: `/api/rorate`,
          method: 'get',
          }).then(res => {
            this.imageUrl=res.data.data
          })
    },
    changeSlider(val){
      this.value3=0
      this.rotate=this.$cookies.get("rotate")
    },
    all(val){
      if(this.value3==0)
      {
        this.rotate=this.$cookies.get("rotate")
        this.$cookies.set("nowValue",0)
      }
      var lastValue=this.$cookies.get("nowValue")
      var nowValue=this.value3
      this.$cookies.set("nowValue",this.value3)

      if(nowValue>lastValue)
        this.rotate=Math.floor(this.rotate)+nowValue-lastValue
      else
      {
        // if(nowValue==lastValue)
        //   this.rotate=Math.floor(this.rotate)-(val)
        // else
          this.rotate=Math.floor(this.rotate)-(lastValue-nowValue)
      }
      console.log(Math.abs(360-this.rotate))
      if(Math.abs(360-this.rotate)<=7)//误差
      {
        this.isTouch=false
        this.rotate=0
        var stuId=this.formLabelAlign.username
        //发送验证码(跟后端接口接触)
        this.$axios({
          url: `/api/send/${stuId}`,
          method: 'get',
          }).then(res => {
          console.log(res.data.data)
          this.valid=res.data.data
          if(res.data.code==200)
          {
            this.$message({
              type:'success',
              message:'验证码已发送到你的邮箱'
            })
            //前端换成倒计时60秒
            this.isShow=false
            this.noShow=true
            if(this.noShow){
              this.downTime()
            }
          }
          if(res.data.code==400)
          {
            this.isShow=false
            this.noShow=false
            this.imageShow=true
            this.showImage()
          }

        }).catch(()=>{
          this.$message({
          type:'error',
          message:'网络出差了'
          })
        })
      }
      // console.log(this.rotate+"qqq"+((lastValue-nowValue)))
    },

    //用户登录请求后台处理
    login() {
      if(this.formLabelAlign.verify=='')
      {

        this.$message({
          type:'error',
          message:'验证码不能为空'
        })
        return
      }


      if(this.countdown)
      {
        console.log("登录操作执行-------");
        this.$axios({
        url: `/api/login`,
        method: 'post',
        data: {
          ...this.formLabelAlign
        }
      }).then(res=>{
        if(res.data.code==200){
          let resData = res.data.data
        if(resData != null) {
          switch(resData.role) {
            case "0":  //管理员
              this.$cookies.set("cname", resData.adminName)
              this.$cookies.set("cid", resData.adminId)

              this.$cookies.set("role", 0)
              var Data = Object.assign({ name: 'token', val: resData.adminId }, { startTime: new Date().getTime() });
              sessionStorage.setItem("token", JSON.stringify(Data));
              this.$router.push({path: '/index' }) //跳转到首页
              break
            case "1": //教师
              this.$cookies.set("cname", resData.teacherName)
              this.$cookies.set("cid", resData.teacherId)
              this.$cookies.set("image",resData.avatar)
              var Data = Object.assign({ name: 'token', val: resData.teacherId }, { startTime: new Date().getTime() });
              sessionStorage.setItem("token", JSON.stringify(Data));
              this.$cookies.set("role", 1)
              this.$router.push({path: '/index' }) //跳转到教师用户
              break
            case "2": //学生
              this.$cookies.set("cname", resData.studentName)
              this.$cookies.set("cid", resData.studentId)
              // sessionStorage.setItem("token",resData.studentId)
              var Data = Object.assign({ name: 'token', val: resData.studentId }, { startTime: new Date().getTime() });
              sessionStorage.setItem("token", JSON.stringify(Data));

              this.$cookies.set("image",resData.avatar)
              this.$router.push({path: '/student'})

              break
          }
        }
        }
        if(res.data.code==350){
          this.$message({
            type:'error',
            message:'验证码错误,请重新输入'
          })
          this.formLabelAlign.verify=''
          }
        if(res.data.code==400) { //被登录了

              let ws=new WebSocket(this.$websocketUrl,res.data.data.currentId)
              //接收数据
              // ws.onmessage=(msg)=>{
              //   let data=JSON.parse(msg.data)
              //   if(data.type==='被登录')
              //     console.log(data.message)
              // }

              //发送数据
              let msobj={}
              msobj.sender=res.data.data.currentId
              msobj.receiver=res.data.data.wantLogin
              msobj.message="is_lock"
              console.log(ws.readyState)
              if (ws.readyState === 1) { // this.websock.readyState = 1 表示连接成功，可以立即发送信息
                ws.send(JSON.stringify(msobj))
              } else if (ws.readyState === 0) { // 表示正在连接，设置300ms后发送信息
                setTimeout(function () {
                ws.send(JSON.stringify(msobj))
                }, 300);
              } else { // 连接未创建或者创建失败，则重新创建连接，并设置500ms后发送信息

                setTimeout(function () {
                  ws.send(JSON.stringify(msobj))
                }, 500);
              }




          this.$message({
            showClose: true,
            type: 'error',
            message: res.data.message
          })
        }
        if(res.data.code==450){
          this.$message({
            showClose: true,
            type: 'error',
            message: res.data.message
          })
        }

      })
      }
      if(this.time==0){
        this.$message({
          type:'error',
          message:'验证码已失效,请重新发送'
        })
        this.formLabelAlign.verify=''
      }
      //不是倒计时的验证码


      if(!this.countdown){
        console.log("登录操作执行-------");
        this.$axios({
        url: `/api/login`,
        method: 'post',
        data: {
          ...this.formLabelAlign
        }
      }).then(res=>{
        if(res.data.code==200){
          let resData = res.data.data
        if(resData != null) {
          switch(resData.role) {
            case "0":  //管理员

              this.$cookies.set("cname", resData.adminName)
              this.$cookies.set("cid", resData.adminId)
              this.$cookies.set("image",resData.avatar)
              this.$cookies.set("role", 0)
              var Data = Object.assign({ name: 'token', val: resData.adminId }, { startTime: new Date().getTime() });
              sessionStorage.setItem("token", JSON.stringify(Data));
              this.$router.push({path: '/index' }) //跳转到首页
              break
            case "1": //教师
              this.$cookies.set("cname", resData.teacherName)
              this.$cookies.set("cid", resData.teacherId)
              this.$cookies.set("image",resData.avatar)
              var Data = Object.assign({ name: 'token', val: resData.teacherId }, { startTime: new Date().getTime() });
              sessionStorage.setItem("token", JSON.stringify(Data));
              this.$cookies.set("role", 1)
              this.$router.push({path: '/index' }) //跳转到教师用户
              break
            case "2": //学生
              this.$cookies.set("cname", resData.studentName)
              this.$cookies.set("cid", resData.studentId)
              // sessionStorage.setItem("token",resData.studentId)
              var Data = Object.assign({ name: 'token', val: resData.studentId }, { startTime: new Date().getTime() });
              sessionStorage.setItem("token", JSON.stringify(Data));

              this.$cookies.set("image",resData.avatar)
              this.$router.push({path: '/student'})

              break
          }
        }
        }
        if(res.data.code==350){
          this.$message({
            type:'error',
            message:'验证码错误,请重新输入'
          })
          this.showImage()
          this.formLabelAlign.verify=''
          }
        if(res.data.code==400) { //被登录了

              let ws=new WebSocket(this.$websocketUrl,res.data.data.currentId)
              //接收数据
              // ws.onmessage=(msg)=>{
              //   let data=JSON.parse(msg.data)
              //   if(data.type==='被登录')
              //     console.log(data.message)
              // }

              //发送数据
              let msobj={}
              msobj.sender=res.data.data.currentId
              msobj.receiver=res.data.data.wantLogin
              msobj.message="is_lock"
              console.log(ws.readyState)
              if (ws.readyState === 1) { // this.websock.readyState = 1 表示连接成功，可以立即发送信息
                ws.send(JSON.stringify(msobj))
              } else if (ws.readyState === 0) { // 表示正在连接，设置300ms后发送信息
                setTimeout(function () {
                ws.send(JSON.stringify(msobj))
                }, 300);
              } else { // 连接未创建或者创建失败，则重新创建连接，并设置500ms后发送信息

                setTimeout(function () {
                  ws.send(JSON.stringify(msobj))
                }, 500);
              }




          this.$message({
            showClose: true,
            type: 'error',
            message: res.data.message
          })
        }
        if(res.data.code==450){
          this.$message({
            showClose: true,
            type: 'error',
            message: res.data.message
          })
        }

      })
      }

    },
    clickTag(key) {
      this.role = key
    }
  },
  computed: mapState(["userInfo"]),
  mounted() {

  }
}
</script>

<style lang="less" scoped>
.remind {
  border-radius: 4px;
  padding: 10px 20px;
  display: flex;
  position: fixed;
  right: 20px;
  bottom: 50%;
  flex-direction: column;
  color: #606266;
  background-color: #fff;
  border-left: 4px solid #409eff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)
}
.container {
  margin-bottom: 32px;
}
.container .el-radio-group {
  margin: 30px 0px;
}
a:link {
  color:#ff962a;
  text-decoration:none;
}
#login {
  font-size: 14px;
  color: #000;
  background-color: #fff;
}
#login .bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow-y: auto;
  height: 100%;
  background: url('../../assets/img/loginbg.png')center top / cover no-repeat;
  background-color: #b6bccdd1 !important;
}
#login .main-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
#login .main-container .top {
  margin-top: 100px;
  font-size: 30px;
  color: #ff962a;
  display: flex;
  justify-content: center;
}
#login .top .icon-kaoshi {
  font-size: 80px;
}
#login .top .title {
  margin-top: 20px;
}
#login .bottom {
  display:flex;
  justify-content: center;
  background-color:#fff;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
#login .bottom .title {
  text-align: center;
  font-size: 30px;
}
.bottom .container .title {
  margin: 30px 0px;;
}
.bottom .submit .row-login {
  width: 100%;
  background-color: #04468b;
  border-color: #04468b;
  margin: 20px 0px 10px 0px;
  padding: 15px 20px;
}
.bottom .submit {
  display: flex;
  justify-content: center;
}
.footer {
  margin-top: 50px;
  text-align: center;
}
.footer .msg1 {
  font-size: 18px;
  color: #fff;
  margin-bottom: 15px;
}
.footer .msg2 {
  font-size: 14px;
  color: #e3e3e3;
  margin-top: 70px;
}
.bottom .options {
  margin-bottom: 40px;
  color: #ff962a;
  display: flex;
  justify-content: space-between;
}
.bottom .options > a {
  color: #ff962a;
}
.bottom .options .register span:nth-child(1) {
  color: #8C8C8C;
}
</style>



